.card-container {
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    flex-wrap: wrap;
}

.card {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    padding: 20px 20px 12px;
    border-radius: 8px;
    margin: 1rem 0.5rem;
    background-color: var(--bg-color--light);
    box-shadow: var(--box-shadow--primary);
}
