.quote {
    overflow-y: hidden;
}

blockquote {
    font-style: italic;
    padding: 0.35em 60px;
    line-height: 1.45;
    position: relative;
    width: max-content;
    margin: auto;
}

blockquote:before {
    display: block;
    padding-left: 10px;
    content: open-quote;
    font-size: 80px;
    position: absolute;
    left: -10px;
    bottom: -20px;
    color: var(--accent-color);
}

blockquote:after {
    content: close-quote;
    display: block;
    padding-right: 10px;
    font-size: 80px;
    position: absolute;
    right: 20px;
    bottom: -20px;
    color: var(--accent-color);
}
