:root {
    --primary-color: #8b88e3;
    --primary-color--dark: #6a68ae;
    --primary-color--darker: #58568f;
    --secondary-color: #53906e;
    --tertiary-color: #ff3864;
    --accent-color: #ff6c11;
    --font-color: #424242;
    --bg-color: #ffffff;
    --bg-color--light: #d7d7d7;
    --heading-color: #292922;

    --box-shadow--primary: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 1px rgba(0, 0, 0, 0.2);
}

[data-theme="dark"] {
    --primary-color: #791e94;
    --primary-color--dark: #650d89;
    --primary-color--darker: #261447;
    --secondary-color: #2de2e6;
    --tertiary-color: #ff3864;
    --accent-color: #ff6c11;
    --font-color: white;
    --bg-color: #282c34;
    --bg-color--light: #3c424b;
    --heading-color: #b5b596;

    --box-shadow--primary: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 1px rgba(0, 0, 0, 0.2);
}
