.gym {
    display: flex;
    align-items: center;
}

.gym-cells {
    display: flex;
}

.gym-tick {
    color: var(--secondary-color);
}

.gym-cross {
    color: var(--tertiary-color);
}

.gym-cell {
    width: 4rem;
    height: 4rem;
    background-color: var(--primary-color--darker);
    margin: 1rem 0.5rem;
    box-shadow: var(--box-shadow--primary);
    text-align: center;
    justify-content: center;
}

.gym-cell p {
    font-size: 1.3rem;
    padding-bottom: 0.5rem;
}
