.App {
    background-color: var(--bg-color);
    min-height: 100vh;
    box-sizing: border-box;
    font-size: calc(10px + 2vmin);
    color: var(--font-color);
    padding: 1rem;
}

.boards {
    position: relative;
}

.boards-add {
    position: absolute;
    bottom: 1rem;
    right: 1rem;
    border-radius: 50%;
    height: 3rem;
    width: 3rem;
    cursor: pointer;
}